.cur-project-card
{
    &__image
    {
        background-color: var(--color-gray);
        margin-bottom: 24px;
        overflow: hidden;
        border-radius: 2px;
        img
        {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        @include l-1024
        {
            margin-bottom: 12px;
        }
    }
    &__desc
    {
        margin-bottom: 8px;
        @include caption;
        color: var(--color-dark-red);
    }
    &__name
    {
        @include header-4;
        @include l-1024
        {
            @include header-5;
        }
    }
}