.news
{
    &-wrap
    {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: -40px;
        @include l-1024
        {
            margin-bottom: -48px;
        }
        @include xm
        {
            margin-bottom: -40px;
        }
        .news-card
        {
            width: calc(100%/3 - 14px);
            margin-right: 20px;
            margin-bottom: 40px;
            &:nth-child(3n)
            {
                margin-right: 0;
                @include l-1024
                {
                    margin-right: 20px;
                }
                @include xm
                {
                    margin-right: 0px;
                }
            }
            &:nth-child(2n)
            {
                @include l-1024
                {
                    margin-right: 0;
                }
            }
            @include l-1024
            {
                width: calc(100%/2 - 10px);
                margin-bottom: 48px;
            }
            @include xm
            {
                width: 100%;
                margin-right: 0px;
                margin-bottom: 40px;
            }
        }
    }
    &-bottom
    {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 64px;
        @include xm
        {
            flex-direction: column-reverse;
            gap: 32px;
        }
    }
    &-sort
    {
        display: flex;
        align-items: center;
        &-text
        {
            font-size: 18px;
            font-weight: 400;
            line-height: 21.6px;
        }
        &-select
        {
            margin-left: 10px;
            select
            {
                border: none;
                color: var(--color-dark-red);
                width: 54px;
                font-size: 18px;
                font-weight: 600;
                line-height: 21.6px;
            }
            .custom-select-container
            {
                .custom-select-opener
                {
                    width: 60px;
                    position: relative;
                    background-color: transparent;
                    color: var(--color-dark-red);
                    font-size: 18px;
                    font-weight: 600;
                    line-height: 21.6px;
                    &[aria-expanded="true"]
                    {
                        &::after {
                            transform: translate(0, -50%) rotate(180deg);
                        }
                    }
                    &::after {
                        position: absolute;
                        top: 50%;
                        right: 0px;
                        transform: translate(0, -50%);
                        content: "";
                        background-image: url("data:image/svg+xml,%3Csvg width='18' height='18' viewBox='0 0 18 18' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M3.96967 6.21967C4.26256 5.92678 4.73744 5.92678 5.03033 6.21967L9 10.1893L12.9697 6.21967C13.2626 5.92678 13.7374 5.92678 14.0303 6.21967C14.3232 6.51256 14.3232 6.98744 14.0303 7.28033L9.53033 11.7803C9.23744 12.0732 8.76256 12.0732 8.46967 11.7803L3.96967 7.28033C3.67678 6.98744 3.67678 6.51256 3.96967 6.21967Z' fill='black'/%3E%3C/svg%3E");
                        width: 18px;
                        height: 18px;
                    }
                }
                .custom-select-panel
                {
                    transition: none;
                    background-color: var(--color-white);
                    .custom-select-option
                    {
                        border: 1px solid var(--color-black);
                        border-bottom: none;
                        cursor: pointer;
                        &:last-child
                        {
                            border-bottom: 1px solid var(--color-black);
                        }
                    }
                }
            }
        }
    }
    &-paggination
    {
        display: flex;
        gap: 30px;
        font-size: 18px;
        font-weight: 400;
        line-height: 21.6px;
        &-links
        {
            display: flex;
            gap: 20px;
            @include xm
            {
                display: none;
            }
        }
        &-link
        {
            @include xm
            {
                display: none;
            }
            span
            {
                color: #A8A8A5;
            }
            a
            {
                color: var(--color-black);
                text-decoration: underline;
            }
        }
        &-nums
        {
            display: flex;
            gap: 40px;
            text-decoration: underline;
        }
        &-num
        {
            &--current
            {
                color: var(--color-dark-red);
                font-weight: 600;
            }
        }
    }
}