.news-card
{
    &__image
    {
        line-height: 0;
        height: 280px;
        margin-bottom: 12px;
        border-radius: 2px;
        overflow: hidden;
        background-color: var(--color-gray);
        img
        {
            object-fit: cover;
            height: 100%;
            width: 100%;
        }
    }
    &__date
    {
        @include caption;
        color: var(--color-gray);
        margin-bottom: 8px;
    }
    &__name
    {
        @include header-5;
        color: var(--color-black);
    }
}
