.detail-news
{
    flex: 0 1 860px;
    margin-right: 30px;
    & > *
    {
        margin-bottom: 32px;
        &:last-child
        {
            margin-bottom: 0;
        }
    }
    @include l-1024
    {
        margin-right: 0;
    }
    & + .page-sidebar
    {
        @include l-1024
        {
            display: none;
        }
    }
    &__main-image
    {
        img
        {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__top
    {

    }
    &__date
    {
        @include caption;
        color: var(--color-gray);
        margin-bottom: 8px;
    }
    &__title
    {
        h1
        {
            @include header-2;
        }
        margin-bottom: 12px;
    }
    &__desc
    {
        @include text-1
    }
    &__content
    {
        & > *
        {
            @include text-2;
            margin-bottom: 32px;
            &:last-child
            {
                margin-bottom: 0;
            }
        }
        img
        {
            width: 100%;
            border-radius: 2px;
        }
    }
    &__slides
    {
        position: relative;
        .swiper-pagination
        {
            bottom: -40px;
            .swiper-pagination-bullet
            {
                width: 8px;
                height: 8px;
                margin: 0 4px;
            }
            .swiper-pagination-bullet-active
            {
                background: var(--color-dark-red)
            }
        }
    }
}