.main-banner
{
    position: relative;
    border-radius: 2px;
    overflow: hidden;
    &__image
    {
        position: relative;
        line-height: 0;
        img
        {
            width: 100%;
            height: 100%;
        }
        &::after
        {
            position: absolute;
            content: "";
            top: 0;
            left: 0;
            z-index: 1;
            width: 100%;
            height: 100%;
            background-color: #000000;
            opacity: 0.3;
        }
    }
    &__text
    {
        max-width: 60%;
        position: absolute;
        bottom: 32px;
        left: 40px;
        z-index: 2;
        color: #fff;
        @include l-1024
        {
            left: 24px;
            max-width: calc(100% - 24px * 2);
        }
        @include xm
        {
            bottom: 24px;
            left: 16px;
            max-width: calc(100% - 16px * 2);
        }
    }
    &__title
    {
        h2
        {
            @include header-1;
            @include xm
            {
                @include header-2;
            }
        }
        text-transform: uppercase;
        margin-bottom: 16px;
        @include xm
        {
            margin-bottom: 8px;    
        }
    }
    &__subtitle
    {
        p
        {
            @include text-1;
            @include xm
            {
                @include text-2
            }
        }
    }
}