.header
{
    position: fixed;
    top: 0;
    z-index: 10;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: var(--color-white);
    transition: top .2s ease;
    &__wrap
    {
        width: 100%;
        padding-top: 24px;
        padding-bottom: 24px;
        @include l-1024
        {
            padding-top: 20px;
            padding-bottom: 20px;
        }
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo
    {
        display: block;
        @include touches;
        color: var(--color-dark-red);
    }
    &__top_menu
    {
        ul
        {
            display: flex;
            gap: 48px;
            @include l-1200
            {
                gap: 16px;
            }
        }
        li
        {
            a
            {
                @include text-2-semibold;
                margin: -5px;
                padding: 5px;
            }
        }
        @include l-1024
        {
            display: none;
        }
    }
    &__button
    {
        @include l-1024
        {
            display: none;
        }
    }
    &__burger
    {
        display: none;
        @include l-1024
        {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: unset;
            @include touches
        }
        .icon-close
        {
            display: none;
        }
    }
    &--active
    {
        min-height: 100vh;
        .header__mobile-menu
        {
            @include l-1024
            {
                display: flex;
            }
        }
        .icon-burger-menu
        {
            display: none;
        }
        .icon-close
        {
            display: block;
        }
    }
    &__mobile-menu
    {
        display: none;
        width: 100%;
        background-color: var(--color-white);
        @include l-1024
        {
            margin-top: 44px;
            margin-bottom: 64px;
            flex: 1;
            flex-direction: column;
            justify-content: space-between;
            &_wrap
            {
                ul
                {
    
                }
                li
                {
                    margin-bottom: 32px;
                    a
                    {
                        @include text-1-semibold;
                        @include touches;
                    }
                }
            }
            &_button
            {

            }
        }
        @include xm
        {
            margin-top: 40px;
        }
    }
}