.input
{
    &__label
    {
        margin-bottom: 8px;
        label
        {
            pointer-events: none;
            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
        }
    }
    &__error
    {
        display: none;
        margin-top: 4px;
        @include caption;
        color: #ff0000;
    }
    &__red
    {
        color: #ff0000;
    }
    input[type="text"],input[type="email"], textarea
    {
        width: 100%;
        padding: 12px 16px;
        border: 2px solid #DBDBDB;
        border-radius: 2px;
        font-size: 16px;
        line-height: 28px;
        &::placeholder
        {
            color: #C8C8C8;
        }
        &:hover
        {
            border: 2px solid var(--color-gray);
        }
        &:focus
        {
            border: 2px solid var(--color-black);
        }
    }
    textarea
    {
        height: 180px;
        resize: none;
    }
    &.error
    {
        input[type="text"],input[type="email"]
        {
            border: 2px solid #ff0000;
        }
        .input__error
        {
            display: block;
        }
    }
    .custom-select-opener
    {
        position: relative;
        width: 100%;
        padding: 12px 16px;
        border: 2px solid #DBDBDB;
        background-color: var(--color-white);
        border-radius: 2px;
        font-size: 16px;
        line-height: 28px;
        &[aria-expanded="true"]
        {
            &::after
            {
                transform: translate(0, -33%) rotate(180deg);
            }
        }
        &::after
        {
            position: absolute;
            top: 50%;
            right: 16px;
            transform: translate(0, -75%);
            content: url("../img/arrow-select.svg");
            width: 16px;
            height: 16px;
        }
        &:hover
        {
            border: 2px solid var(--color-gray);
        }
        &:focus
        {
            border: 2px solid var(--color-black);
        }
    }
    .custom-select-panel
    {
        background-color: var(--color-white);
        transition: none;
    }
    .custom-select-option
    {
        cursor: pointer;
        padding: 12px 16px;
        font-size: 16px;
        line-height: 28px;
        border-bottom: 2px solid var(--color-gray);
        border-left: 2px solid var(--color-gray);
        border-right: 2px solid var(--color-gray);
    }
    &-file
    {
        margin-top: 12px;
        &__link
        {
            display: flex;
            &-el
            {
                display: flex;
                align-items: center;
                gap: 8px;
                cursor: pointer;
                span
                {
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 22px;
                    text-decoration: underline;
                    color: var(--color-dark-red);
                }
            }
            input
            {
                display: none;
            }
        }
        &__clip
        {
            width: 24px;
            height: 24px;
        }
    }
}

.custom-checkbox > input {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }
  
  .custom-checkbox > span {
    display: inline-flex;
    align-items: center;
    user-select: none;
    font-size: 14px;
    line-height: 24px;
    a
    {
        margin-left: 8px;
        color: var(--color-dark-red);
        text-decoration: underline;
    }
  }
  
  .custom-checkbox > span::before {
    content: '';
    display: inline-block;
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 2px solid var(--color-dark-red);
    margin-right: 8px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100% 50%;
  }
  
  /*.custom-checkbox > input:not(:disabled):not(:checked) + span:hover::before {
