.footer
{
    display: flex;
    flex-direction: column;
    background-color: var(--color-dark-red);
    color: var(--color-white);
    &__wrap
    {
        display: flex;
        flex-direction: column;
        gap: 80px;
        width: 100%;
        padding-top: 40px;
        padding-bottom: 40px;
        @include l-1024
        {
            gap: 100px;
            padding-top: 20px;
            padding-bottom: 24px; 
        }
        @include xm
        {
            gap: 48px;
            padding-top: 40px;
            padding-bottom: 32px; 
        }
    }
    &__top
    {
        display: flex;
        justify-content: space-between;
        @include xm
        {
            flex-direction: column;
            align-items: center;
            gap: 48px;
        }
    }
    &__bottom
    {
        display: flex;
        justify-content: space-between;
        @include l-1024
        {
            flex-direction: column;
            align-items: center;
            gap: 48px;
        }
    }
    &__logo
    {
        display: block;
        @include touches;
        color: var(--color-white);
    }
    &__contacts
    {
        text-align: right;
        @include xm
        {
            text-align: center;
        }
        &-name
        {
            margin-bottom: 8px;
            @include text-2-medium;
        }
        &-phone
        {
            @include header-3;
            a
            {
                @include touches;
            }
        }
    }
    &__menu
    {
        ul
        {
            display: flex;
            gap: 55px;
            @include l-1200
            {
                gap: 16px;
            }
            @include xm
            {
                flex-direction: column;
                align-items: center;
                gap: 24px;
            }
        }
        li
        {
            a
            {
                @include text-2-medium;
                margin: -5px;
                padding: 5px;            
            }
        }
    }
    &__info
    {
        @include text-2-medium;
        @include xm
        {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 4px;
        }
    }
}