.docs
{
    display: flex;
    flex-direction: column;
    gap: 40px;
    max-width: 860px;
    flex: 1 1 860px;
    margin-right: 30px;
    @include l-1024
    {
        margin-right: 0;
    }
    &-page
    {
        margin-top: 64px;
        @include l-1024
        {
            margin-top: 80px;
        }
        @include xm
        {
            margin-top: 64px;
        }
    }
    &-section
    {
        padding-bottom: 40px;
        border-bottom: 1px solid #D9D9D9;

        &:last-child
        {
            border-bottom: none;
            @include l-1024
            {
                padding-bottom: 16px;
            }
            @include xm
            {
                padding-bottom: 0px;
            }
        }
        &__title
        {
            @include header-2;
            margin-bottom: 28px
        }
        &__list
        {
            display: flex;
            flex-direction: column;
            gap: 28px;
        }
    }
}

