.project-card
{
    display: block;
    position: relative;
    border-top: 1px solid var(--color-black);
    border-bottom: 1px solid var(--color-black);
    border-right: 1px solid var(--color-black);
    border-left: 1px solid var(--color-black);
    @include xm
    {
        &:first-child
        {
            border-top: 1px solid var(--color-black);
        }
        border-top: none;
    }
    &:nth-child(2n)
    {
        border-left: none;
        @include xm
        {
            border-left: 1px solid var(--color-black);
        }
    }
    &:nth-child(n + 3)
    {
        border-top: none;
    }
    &:hover
    {
        .project-card__plate
        {
            transition: all .3s ease;
            opacity: 0;
        }
        .project-card__name, .project-card__num, .project-card__arrow
        {
            transition: all .3s ease;
            color: var(--color-white);
        }
    }
    &__image
    {
        line-height: 0;
        height: 100%;
        background-color: var(--color-gray);
        img
        {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__plate
    {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        background-color: #fff;
    }
    &__filter
    {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background-color: #00000040;
    }
    &__name
    {
        position: absolute;
        top: 24px;
        left: 24px;
        width: calc(100% - 48px);
        z-index: 3;
        color: var(--color-black);
        @include header-2;
        @include xm
        {
            @include header-4;
        }
    }
    &__num
    {
        position: absolute;
        bottom: 24px;
        left: 24px;
        z-index: 3;
        color: var(--color-black);
        font-size: 24px;
        font-weight: 600;
        line-height: 22px;
        @include xm
        {
            line-height: 28px;
        }
    }
    &__arrow
    {
        position: absolute;
        bottom: 24px;
        right: 24px;
        z-index: 3;
        color: var(--color-dark-red);
    }
}