body
{
    color: var(--color-black);
    font-family: "Raleway", sans-serif;
}
.main
{
    min-height: 70vh;
    margin-top: 100px;
    margin-bottom: 100px;
    padding-top: 40px;
    @include l-1024
    {
        margin-top: 92px;
    }
    @include xm
    {
        margin-bottom: 80px;
        padding-top: 24px;
    }
}
.container
{
    max-width: calc(1360px + 2*40px);
    padding-left: 40px;
    padding-right: 40px;
    margin: 0 auto;
    @include l-1024
    {
        padding-left: 20px;
        padding-right: 20px;
    }
    @include xm
    {
        padding-left: 16px;
        padding-right: 16px;
    }
}
.heading-title
{
    margin-bottom: 40px;
    @include xm
    {
        margin-bottom: 32px;
    }
    h1,h2
    {
        @include header-1;
        text-transform: uppercase;
        @include xm
        {
            @include header-2-caps;
        }
    }
}
.page-banner
{
    border-radius: 2px;
    overflow: hidden;
    background-color: #D9D9D9;
    margin-bottom: 64px;
    img
    {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.page-with-sidebar
{
    display: flex;
    justify-content: space-between;
    @include l-1024
    {
        display: block;
    }
}
.page-content
{
    h2
    {
        @include header-2;
        @include xm
        {
            @include header-3;
        }
        margin-bottom: 16px;
    }
    p
    {
        @include text-2;
        margin-bottom: 16px;
    }
}
.page-sidebar
{
    flex-shrink: 0;
    width: 310px;
    @include l-1024
    {
        width: 100%;
        margin-top: 64px;
        .swiper-slide
        {
            width: 310px;
        }
    }
    &__title
    {
        h2
        {
            @include header-4-caps;
        }
        color: var(--color-black);
        text-transform: uppercase;
        margin-bottom: 32px;
        @include l-1024
        {
            margin-bottom: 24px;
        }
    }
    &__news-card
    {
        .news-card__date
        {
            @include caption;
            color: var(--color-black);
            margin-bottom: 12px;
            @include l-1024
            {
                margin-bottom: 8px;
                color: var(--color-gray);
            }
        }
        .news-card__name
        {
            @include header-5;
            color: var(--color-black);

        }
    }
}