.button
{
    display: block;
    cursor: pointer;
    min-width: 168px;
    padding: 16px;
    background-color: var(--color-dark-red);
    border-radius: 2px;
    border: none;
    transition: var(--transition-default);
    @include caption-semibold;
    text-decoration: none;
    text-align: center;
    color: var(--color-white);
    &:hover
    {
        background-color: var(--color-red);
    }
}