.contact
{
    &-map
        {
        display: flex;
        gap: 24px;
        @include l-1024
        {
            flex-direction: column-reverse;
        }
        @include xm
        {
            gap: 16px;
        }
        &__info
        {
            flex: 1 0 420px;
            padding: 32px;
            border-radius: 2px;
            background-color: #F6F6F6;
            @include l-1024
            {
                flex: 1;
                padding: 24px;
                background-color: transparent;
                border: 2px solid var(--color-black);
            }
            @include xm
            {
                padding: 20px;
            }
            &-items
            {
                margin-bottom: 32px;
                display: grid;
                gap: 24px;
                grid-template-areas:
                "a"
                "b"
                "c";
                @include l-1024
                {
                    grid-template-areas:
                    "b a"
                    "b c";
                }
                @include xm
                {
                    grid-template-areas:
                    "a"
                    "b"
                    "c";
                }
            }
            &-item--1
            {
                grid-area: a;
            }
            &-item--2
            {
                grid-area: b;
            }
            &-item--3
            {
                grid-area: c;
            }
            &-item
            {
                &-title
                {
                    @include text-2-semibold;
                    color: var(--color-dark-red);
                    margin-bottom: 4px;
                    @include l-1024
                    {
                        margin-bottom: 6px;
                    }
                }
                &-content
                {
                    @include text-2-medium;
                    color: var(--color-black);
                    & > *
                    {
                        margin-bottom: 12px;
                        &:last-child
                        {
                            margin-bottom: 0;
                        }
                        @include l-1024
                        {
                            margin-bottom: 0;
                        }
                    }
                }
            }
            &-button
            {

            }
        }
        &__map
        {
            flex: 2 1 916px;
            background-color: var(--color-gray);
            border-radius: 2px;
            overflow: hidden;
            @include l-1024
            {
                flex: 1 1 410px;
            }
        }
        &__yandex
        {
            height: 100%;
            .ymaps-2-1-79-map-copyrights-promo, .ymaps-2-1-79-copyright__wrap
            {
                display: none;
            }
        }
    }
}