.last-news
{
    &__header
    {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 40px;
        .last-news__link
        {
            @include xm
            {
                display: none
            }
        }
    }
    &__bottom
    {
        display: none;
        @include xm
        {
            display: block;
            margin-top: 32px;
        }
    }
    &__title
    {
        h2
        {
            @include header-1;
            text-transform: uppercase;
            @include xm
            {
                @include header-2-caps;
            }
        }
    }
    &__link
    {
        display: flex;
        align-items: center;
        gap: 4px;
        @include header-5;
        .icon
        {
            color: var(--color-dark-red);
        }
        &:hover
        {
            color: var(--color-red);
            .icon
            {
                color: var(--color-red);
            }
        }
    }
    &__card
    {
        width: calc(100%/4 - 25px);
        @include l-1200
        {
            width: calc(100%/4 - 4px);
        }
        @include l-1024
        {
            min-width: 310px;
            width: calc(100%/3);
        }
        &:last-child
        {
            margin-right: 0 !important;
        }
        &--first
        {
            position: relative;
            height: 380px;
            width: calc(100%/2 - 25px);
            border-radius: 2px;
            overflow: hidden;
            @include l-1200
            {
                height: 310px;
            }
            &:after
            {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
                background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.75) 100%);
            }
            .last-news__card-image
            {
                height: 100%;
            }
            .last-news__card-info
            {
                color: var(--color-white);
                width: calc(100% - 24px*2);
                position: absolute;
                z-index: 2;
                bottom: 24px;
                left: 24px;
            }
            .last-news__card-date
            {
                color: var(--color-white);
                margin-bottom: 16px;
            }
            .last-news__card-name
            {
                @include header-4
            }
            @include l-1024
            {
                position: relative;
                height: auto;
                width: calc(100%/3);
                min-width: 310px;
                border-radius: 0;
                overflow: hidden;
                &:after
                {
                    content: unset;
                }
                .last-news__card-image
                {
                    height: 210px;
                }
                .last-news__card-info
                {
                    color: inherit;
                    width: 100%;
                    position: static;
                }
                .last-news__card-date
                {
                    color: var(--color-gray);
                    margin-bottom: 8px;
                }
                .last-news__card-name
                {
                    @include header-5
                }
            }
        }
        &-image
        {
            width: 100%;
            height: 210px;
            border-radius: 2px;
            overflow: hidden;
            background-color: var(--color-gray);
            @include l-1200
            {
                height: 170px;
            }
            @include l-1024
            {
                height: 210px;
            }
            img
            {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &-info
        {
            margin-top: 16px;
        }
        &-date
        {
            color: var(--color-gray);
            margin-bottom: 12px;
            @include caption;
            @include l-1024
            {
                margin-bottom: 8px;
            }
        }
        &-name
        {
            @include header-5;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 4; /* Ограничение в 3 строки */
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }
    
}