.breadcrumbs
{
    display: flex;
    flex-wrap: wrap;
    gap: 4px;
    margin-bottom: 24px;
    @include caption;
    @include xm
    {
        margin-bottom: 16px;
    }
    &__item {
        a {
            color: var(--color-black);
            &:hover
            {
                color: var(--color-red);
            }
        }
        span
        {
            color: var(--color-dark-red);
        }
    }
    &__slash
    {

    }
}