.vakansii
{
    display: flex;
    flex-direction: column;
    gap: 24px;
    max-width: 780px;
    margin-right: 30px;
    @include l-1024
    {
        max-width: unset;
        margin-right: 0;
    }
    &-card
    {
        padding: 32px;
        border: 2px solid var(--color-black);
        border-radius: 2px;
        @include xm
        {
            padding: 20px 16px;
        }
        &__name
        {
            margin-bottom: 8px;
            h2
            {
                @include header-2;
                @include xm
                {
                    @include header-3;
                }
            }
        }
        &__price
        {
            @include text-1-semibold;
            margin-bottom: 16px;
        }
        &__desc
        {
            @include text-2;
        }
        &__treb
        {
            margin-top: 24px;
            &-name
            {
                @include text-1-semibold;
                margin-bottom: 14px;
            }
            &-list
            {
                margin-bottom: 32px;
                ul
                {
                    display: flex;
                    flex-direction: column;
                    gap: 15px;
                    li
                    {
                        position: relative;
                        padding-left: 27px;
                        @include text-2;
                        &::before
                        {
                            position: absolute;
                            content: "";
                            top: 12px;
                            left: 0px;
                            width: 16px;
                            height: 4px;
                            background-color: var(--color-dark-red);
                        }
                    }
                }
            }
        }
        &__button
        {
            display: flex;
        }
    }
}